import {type FC } from "react";
import { useTranslation } from "react-i18next";
import HexImage from "@/components/ui/HexImage";
const research_image = "";
const ResearchImage: FC = () => {
    const { t } = useTranslation();
    return <HexImage
        hexData={research_image}
        imageType="jpg"
        alt={t(`homepage.services.research.title`)}
        aspectRatio="square"
        className="w-full"
    />
}
export default ResearchImage;